.download-pdf {
  background-color: #009EE2;
  min-height: 100vh;
  height: auto;
  padding: 25px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .download-pdf .content-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 476px;
    height: 80%; }
    .download-pdf .content-body .text-area {
      padding: 25px 0px; }
      .download-pdf .content-body .text-area .title {
        font-family: 'GothamNarrow-Book';
        font-size: 40px;
        color: white;
        margin: 0px;
        text-align: center; }
      .download-pdf .content-body .text-area .body-text {
        font-family: 'GothamNarrow-Book';
        font-size: 20px;
        line-height: 25px;
        color: white;
        margin: 0px;
        text-align: center;
        margin: 15px; }
    .download-pdf .content-body .button-area {
