        padding: 0px 10px;
        font-family: 'GothamNarrow-Book';
        font-size: 20px;
        color: #1b1919;
        margin: 0px;
        text-align: center;
        margin: 15px; }
      .download-pdf .content-body .button-area .button-theme:focus {
        text-decoration: none;
        outline: none; }
      .download-pdf .content-body .button-area .button-theme:hover {
        text-decoration: none;
        outline: none; }
      @media screen and (max-width: 767px) {
        .download-pdf .content-body .button-area .button-theme {
          width: auto;
          padding: 0px 25px; } }
      .download-pdf .content-body .button-area .btn-transparent {
        background: none;
        border: none;
        font-family: 'GothamNarrow-Book';
        font-size: 20px;
        color: white;
